*{
  margin:0;
  padding:0;
}

.container{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #162938;
  color: #fff;
}

.flex-wrapper{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  width:50%;
}

.column-wrapper{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.box-container{
  border: 2px solid #BD632F;
  border-top: none;
  padding: 1rem;
  width:35%;
  height: 40vh;
  position: relative;
}

.box-container::before {
  content: "";
  width: 7%;
  height: 2px;
  border-radius: 0 50% 50% 0;
  background-color: #BD632F;
  position: absolute;
  top: 0;
  left: -1px;
}

.box-container::after {
  content: "";
  width: 45%;
  height: 2px;
  border-radius: 50% 0 0 50%;
  background-color: #BD632F;
  position: absolute;
  top: 0;
  right: 0;
}

.box-container:first-child::after {
  width: 50%;
}

.box-container-options {
  overflow-y: scroll;
  max-height: 100%;
  scrollbar-width: none;
}

.box-container-options::-webkit-scrollbar {
  display: none;
}


.box-container h1{
  position: absolute;
  top: 0;
  left:10%;
  transform: translateY(-60%);
}

i{
  font-size: 27px;
}

button{
  appearance: none;
  outline: none;
  border: none;
  background-color: transparent;
  margin-bottom: 2rem;
  color: #fff;
}

button:hover{
  color: #BD632F;
  cursor: pointer;
}

button:disabled{
  cursor: default;
  color: #dfe6ec;
}

input[type='checkbox']{
  display: none;
}

.box-container label {
  padding: 0.5rem;
  margin-top: 0.5rem;
  cursor: pointer;
  width: 100%;
  display: block;
}

.box-container label:hover {
  background-color:#dfe6ec;
  color: #BD632F;
}

.box-container .checked {
  background-color: #dfe6ec;
  color: #BD632F;
}

.box-container .disabled {
  background-color: #dfe6ec;
  color: #80858a;
  cursor: default;
}

.box-container .disabled:hover {
  background-color: #dfe6ec;
  color: #80858a;
  cursor: default;
}